import { createTheme, ThemeProvider } from '@mui/material/styles';
import { FaRegCopy } from 'react-icons/fa';
import React, { useState, useEffect, useCallback } from 'react';
import { Typography, TextField, Grid, FormControlLabel, Checkbox, Snackbar, InputAdornment, IconButton } from '@mui/material';
import MainCard from 'ui-component/cards/MainCard';
import MuiAlert from '@mui/material/Alert';

const darkTheme = createTheme({
  palette: {
    mode: 'dark'
  }
});

const Page = () => {
  const [message, setMessage] = useState('');
  const [lookalikesValue, setLookalikesValue] = useState(true);
  const [chattyValue, setChattyValue] = useState(false);
  const [fossabotRegExValue, setFossabotRegExValue] = useState(false);
  const [startValue, setStartValue] = useState('\\b');
  const [endValue, setEndValue] = useState('\\b');
  const [contentValue, setContentValue] = useState('([<letter>][\\W_]*?)+');
  const [outputMessage, setOutputMessage] = useState('');
  const [isCopied, setIsCopied] = useState(false);

  useEffect(() => {
    document.title = `LsCo Software - RegEx Generator`;

    const params = new URLSearchParams(window.location.search);

    if (params.get('msg')) {
      setMessage(params.getAll('msg').join('\n'));
      console.log('msg', params.get('msg'));
    }

    if (params.get('start')) {
      setStartValue(params.get('start'));
      console.log('start', params.get('start'));
    }

    if (params.get('end')) {
      setEndValue(params.get('end'));
      console.log('end', params.get('end'));
    }

    if (params.get('group')) {
      setContentValue(params.get('group'));
      console.log('group', params.get('group'));
    }
  }, []);

  const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
  });

  const handleFossabotRegExChange = (e) => {
    const { checked } = e.target;
    setFossabotRegExValue(checked);

    if (chattyValue) {
      setChattyValue(!checked);
    }
  };

  const handleChattyChange = (e) => {
    const { checked } = e.target;
    setChattyValue(checked);

    if (fossabotRegExValue) {
      setFossabotRegExValue(!checked);
    }
  };

  const handleClick = useCallback(
    (msg) => {
      const trimmedMessageArray = msg.split('\n').map((i) => i.trim());
      if (!contentValue.match(/<letter>/gi)) {
        return setOutputMessage('<letter> not found in Group');
      }

      const list = [];

      for (let i = 0; i < trimmedMessageArray.length; i++) {
        const filteredRegex = /[a-züäö0-9]/gi;
        const filteredMessage = trimmedMessageArray[i].match(filteredRegex);

        if (!filteredMessage) {
          continue;
        }

        const regex = filteredMessage
          .map((char) => {
            const replacerRegex = new RegExp(
              contentValue.replace(/<letter>/gi, lookalikesValue ? replacer(char.toLowerCase()) : char.toLowerCase()),
              'gi'
            );
            return replacerRegex.source;
          })
          .join('');

        list.push(regex);
      }

      const output = list
        .map((i) => {
          if (chattyValue) {
            return `config:any regi:${startValue}${i}${endValue}`;
          } else if (fossabotRegExValue) {
            return `regex:${startValue}${i}${endValue}`;
          } else {
            return `${startValue}${i}${endValue}`;
          }
        })
        .join('\n');

      setOutputMessage(output);
    },
    [chattyValue, fossabotRegExValue, lookalikesValue, startValue, contentValue, endValue]
  );

  const handleCopyClick = () => {
    navigator.clipboard.writeText(outputMessage);
    setIsCopied(true);
  };

  const handleClose = () => {
    setIsCopied(false);
  };

  useEffect(() => {
    handleClick(message);
  }, [message, chattyValue, fossabotRegExValue, lookalikesValue, handleClick]);

  function replacer(x) {
    const lookalikes = [
      {
        char: 'a',
        replacements: ['ä', '@', '4', '%']
      },
      {
        char: 'e',
        replacements: ['€', '³', '3']
      },
      {
        char: 'g',
        replacements: ['9']
      },
      {
        char: 'i',
        replacements: ['|', '1', '!']
      },
      {
        char: 'o',
        replacements: ['ö', '0']
      },
      {
        char: 's',
        replacements: ['$']
      },
      {
        char: 'u',
        replacements: ['ü']
      }
    ];

    const find = lookalikes.find((i) => i.char === x);

    if (!find) return x;

    return x + find.replacements.join('');
  }

  return (
    <MainCard title="RegEx Generator" description="You can create a regular expression for your benefit.">
      <Typography variant="body2">
        <Grid container spacing={2} alignItems="center">
          <Grid item xs={12}>
            <ThemeProvider theme={darkTheme}>
              <TextField
                fullWidth
                multiline
                id="outlined-basic"
                minRows={5}
                label="Message"
                type="text"
                variant="outlined"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
              />
            </ThemeProvider>
          </Grid>
          <Grid item xs={12} sm={4}>
            <ThemeProvider theme={darkTheme}>
              <TextField
                fullWidth
                multiline
                id="outlined-start-adornment"
                InputProps={{
                  startAdornment: <InputAdornment position="start">Start</InputAdornment>
                }}
                variant="outlined"
                value={startValue}
                onInput={(event) => setStartValue(event.target.value)}
              />
            </ThemeProvider>
          </Grid>
          <Grid item xs={12} sm={4}>
            <ThemeProvider theme={darkTheme}>
              <TextField
                required
                fullWidth
                multiline
                id="outlined-start-adornment"
                InputProps={{
                  startAdornment: <InputAdornment position="start">Group</InputAdornment>
                }}
                variant="outlined"
                value={contentValue}
                onInput={(event) => setContentValue(event.target.value)}
              />
            </ThemeProvider>
          </Grid>
          <Grid item xs={12} sm={4}>
            <ThemeProvider theme={darkTheme}>
              <TextField
                fullWidth
                multiline
                id="outlined-start-adornment"
                InputProps={{
                  startAdornment: <InputAdornment position="start">End</InputAdornment>
                }}
                variant="outlined"
                value={endValue}
                onInput={(event) => setEndValue(event.target.value)}
              />
            </ThemeProvider>
          </Grid>
          <Grid item xs={12}>
            <FormControlLabel
              control={<Checkbox checked={lookalikesValue} onChange={(e) => setLookalikesValue(e.target.checked)} defaultChecked />}
              label="Lookalikes"
            />
            <FormControlLabel
              control={<Checkbox checked={fossabotRegExValue} onChange={handleFossabotRegExChange} />}
              label="Fossabot RegEx"
            />
            <FormControlLabel control={<Checkbox checked={chattyValue} onChange={handleChattyChange} />} label="Chatty RegEx" />
            <hr />
          </Grid>
          <Grid item xs={12}>
            {outputMessage && (
              <>
                <ThemeProvider theme={darkTheme}>
                  <TextField
                    disabled
                    fullWidth
                    multiline
                    id="outlined-basic"
                    type="text"
                    variant="outlined"
                    value={outputMessage}
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <IconButton aria-label="Copy" onClick={handleCopyClick}>
                            <FaRegCopy />
                          </IconButton>
                        </InputAdornment>
                      )
                    }}
                  />
                </ThemeProvider>
              </>
            )}
            {isCopied && (
              <Snackbar open={isCopied} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
                  Copied to clipboard!
                </Alert>
              </Snackbar>
            )}
          </Grid>
        </Grid>
      </Typography>
    </MainCard>
  );
};

export default Page;
